<template>
  <div>
    <div class="title">
        维修记录
    </div>
    <div class="block">
      <el-timeline>
        <el-timeline-item v-for="(item,index) in Djilv" :key="index" :timestamp="item.dm_time" placement="top">
          <el-card>
            <h4 class="reh4">{{item.dm_d_detail}}</h4>
            <p class="rep">
              操作人：{{item.dmo_name}}
              <span class="rep_right">
                <el-button @click="Fndeletemaintainrecord(item.id)" type="danger">删除</el-button>
              </span>
            </p>
          </el-card>
        </el-timeline-item>
      </el-timeline>
      <!-- <button   -->
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        @current-change="handleCurrentChange"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
  data(){
    return{
      Djilv:[],
      total:0,//分页-总条目
      currentPage:1//分页-当前页数
    };
  },
  methods:{
    Fnmaintainrecordlist(){//查看设备所有维修记录
      url.maintainrecordlist({
        "user_id": this.buserInfo.user_id,
        "dm_d_id": 1,//设备id
        "page":this. currentPage
      }).then(res=>{
        console.log(res,'-------------查看设备的维修记录')
        let xx=res.data.data
        let newlist=[]
        xx.forEach(element => {
        element.fields.id=element.pk
        newlist.push(element.fields)
        });
        this.Djilv=newlist
        this.total=res.data.datacount
      })
    },
    Fndeletemaintainrecord(id){//删除
      url.deletemaintainrecord({
        "user_id": this.buserInfo.user_id,
        "id": id
      }).then(res=>{
        console.log(res,'---------------删除')
        this.Fnmaintainrecordlist()
      })
    },
    handleCurrentChange(val) {//点击分页功能实现数据切换
      this.currentPage=val
      console.log(this.currentPage,'----当前页数')
      this.Fnmaintainrecordlist()
    }
  },
  mounted(){
    //调用查看设备所有维修记录
    this.Fnmaintainrecordlist()
  },
  computed:{
    ...mapState(['buserInfo'])
  }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.block
    margin 30px
.reh4
  font-size 16px
  line-height 30px
  color #333
.rep
  font-size 14px
  color #666
  margin-top 20px
  position relative
  .rep_right
    float right
    display block
</style>


